@import './_variables';
@import './_common';

#header {
    .top-navbar {
        height: 72px;

        &>.container {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        .icon {
            font-size: $large-font;
            font-weight: bold;
            padding: 0.5rem 0;
            cursor: pointer;
        }

        .nav-items {
            display: flex;
            justify-content: space-evenly;
            align-items: center;

            .nav-item {
                margin-left: 16px;
            }
        }

        .menu-options {
            position: absolute;
            top: 64px;
            right: 10%;
            z-index: 1;
            width: 180px;
            height: 280px;
            padding: 8px 0;
            color: $white-color;
            background-color: $secondary-color;
            opacity: 0.9;

            @media screen and (max-width: 768px) {
                right: 5%;
            }

            .menu-option {
                font-size: $small-font;
                padding: 12px 20px;
                cursor: pointer;

                &:hover {
                    font-weight: bold;
                    color: $primary-color;
                    transform: translateX(10%);
                    transition: all 0.3s linear;
                }
            }
        }
    }

    #hero {
        position: relative;
        height: 584px;
        overflow: hidden;

        @media screen and (max-width: 768px) {
            height: 520px;
        }

        &>.container {
            position: absolute;
            top: 10%;
            left: 10%;
            height: 500px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            @media screen and (max-width: 768px) {
                top: 30%;
                left: 50%;
                transform: translate(-50%, -30%);
                text-align: center;
                width: 90%;
            }

            .hero-bio {
                margin-top: 48px;

                .more-info {
                    font-weight: bold;
                    padding: 12px;
                    border: 2px solid $primary-color;
                    cursor: pointer;
                }
            }

            .hero-footer {
                margin-bottom: 28px;

                .social-links {
                    font-weight: bold;
                    font-size: $large-font;

                    .link {
                        padding: 8px 12px;
                        cursor: pointer;

                        &:hover {
                            color: $primary-color;
                        }
                    }
                }
            }
        }

        img {
            display: inline-block;
            position: absolute;
            z-index: -1;
            top: -6%;
            right: -12%;
            width: 40%;
            height: auto;
            max-height: 300%;
            border-radius: 50%;
            opacity: 0.9;

            @media screen and (max-width: 768px) {
                top: -12%;
                right: -25%;
                width: 125%;
                border-radius: 0;
                opacity: 0.1;
            }
        }
    }
}